* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  body {
    background-image: linear-gradient(to right, rgb(191, 191, 191) , rgb(255, 255, 255))  } 

.contact{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 800px;
    min-height: 600px;
    background: rgb(142, 142, 142);
    text-align: center;
    margin: 32px auto;
    border-radius: 20px;
    padding-bottom: 32px;
    padding-left: 30px;
    padding-right: 30px;
    font-size: medium;

}
.button{
  background: rgb(201, 201, 201);
  padding: 5px;
}